@keyframes invisible {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.header {
  & {
    width: 100%;
    display: flex;
    background: rgba(0, 0, 0, 0.637);
    justify-content: space-between;
    align-items: center;
    line-height: 200%;
    min-width: 800px;
    font-size: 2.5rem;
    font-weight: 500;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.383);
    justify-content: space-evenly;
    position: absolute;
    opacity: 0;
    z-index: 999;
    transition: opacity 1s;
    animation: invisible 3s;
  }

  @media only screen and (max-width: 1400px) {
    font-size: 1.5rem;
  }

  @media only screen and (min-width: 220px) {
    .nav-bar {
      padding-right: 20%;
    }
  }

  &:hover {
    opacity: 1 !important;
    animation: none;
  }

  &__content {
    color: white;
    min-width: 150px;
    flex-basis: 30%;
    display: flex;
  }
  &__title {
    padding-left: 20px;
    font-family: Brush Script MT, Chalkduster;
  }
}


.nav-bar {
  height: 100%;
  flex-basis: 70%;
  display: flex;
  padding-right: 30px;
  justify-content: flex-end;
  line-height: 100%;
  align-items: center;
  box-sizing: border-box;
}

.nav-tab {
  & {
    height: 100%;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    font-size: 1.8rem;
    justify-items: center;
  }

  > a {
    cursor: pointer;
    box-sizing: border-box;
    height: 100%;
    display: flex;
    align-items: center;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.6);
    &:hover {
      color: white;
      border-top: 5px solid rgba(0, 0, 0, 0);
      border-bottom: 5px solid rgb(226, 226, 226);
    }
  }
  
  &--active {
    color: rgba(255, 255, 255, 0.8);
    border-top: 5px solid rgba(0, 0, 0, 0);
    border-bottom: 5px solid rgba(224, 223, 223, 0.4);
  }

  &__text {
    padding-left: 30px;
    padding-right: 30px;
  } 
}

